*{
  box-sizing: border-box;
}
body {
  margin: 0;
  color: #050505;
  font-size: 14px;
  background: #fff;
  font-family: "Noto Serif", monospace;
  line-height: 1.4;
  background: url(./Images/Background.jpg) #f2f2f2;
  background-position: 0px 0px;
  background-repeat: repeat-x;
  -webkit-animation: animatedBackground 5000s linear infinite;
  animation: animatedBackground 5000s linear infinite;
}

@keyframes animatedBackground {
  0% {
    background-position: 0 0;
    }
  100% {
    background-position: 99000px 0;
    }
}
body.Move{
  overflow-x: hidden;
}
@media (min-width: 1200px){
  .container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1264px;
  }
}