a,div.asLink{
  cursor: pointer;
  color: black;
  text-decoration: none;
  &:hover{
      text-decoration: none;
      color:#d69162 ;
  }
}
.card{
  transition: .5s;
  -webkit-transition: .5s;
  -moz-transition: .5s;
  -ms-transition: .5s;
  -o-transition: .5s;
  &:hover{
      -o-box-shadow: 2px -1px 33px -4px rgba(0,0,0,0.76);
      -webkit-box-shadow: 2px -1px 33px -4px rgba(0,0,0,0.76);
      -moz-box-shadow: 2px -1px 33px -4px rgba(0,0,0,0.76);
      box-shadow: 2px -1px 33px -4px rgba(0,0,0,0.76);
  }
  &:hover p{
      color: black;
  }
}
p{
  font-family: Arial, Helvetica, sans-serif;
}
.AboutSection p{
  font-family: "Noto Serif";
  line-height: 25px;
}
.Main-Header{
  padding: 30px 0 35px;
  .head{
    font-size: 65px;
  }
}
.main{
  margin: 120px 0px ;
}
.main-hover{
  &:hover{
      color: #d69162;
  }
}
.pagination{
  transition: .5s;
  -webkit-transition: .5s;
  -moz-transition: .5s;
  -ms-transition: .5s;
  -o-transition: .5s;
  .page-item.disabled{
    opacity: .5;
  }
  .page-item{
    margin: 0 5px;
      .page-link {
          font-size: 20px;
          font-weight: 600;
          color: black;
          &:hover{background-color: #d69162}
          &:focus{box-shadow: none;}
      }
  }
  .page-item.active .page-link {
    z-index: 3;
    border-color: #d69162;
  }
  .page-item.active{
      .page-link {
          color: white;
          background-color: #d69162;
          &:hover{background-color: #d69162;}
          
      }
  }  
}